//èŽ·å–canvasèŠ‚ç‚¹
let canvas = document.getElementById("canvas")
//å°ºå¯¸è‡ªé€‚åº”å±å¹•
canvas.width = document.documentElement.clientWidth
canvas.height = document.documentElement.clientHeight
//è®¾ç½®ç‚¹ç±»åž‹
let ctx = canvas.getContext("2d")
//è®¾ç½®çº¿æ ·å¼
ctx.strokeStyle = "black"
//è®°å½•ä¸Šæ¬¡ç”»ç¬”
let lastsStrokeStyle
//è®¾ç½®çº¿å®½åº¦
ctx.lineWidth = 5
//è®¾ç½®åœ†è§’æŽ¥å£
ctx.lineCap = "round"

//å£°æ˜Žå¼€å¯/å…³é—­
let painting = false

//ä½ç½®æ•°æ®åˆ—è¡¨
let historyDeta = [];
//ä¿å­˜æ•°æ®
function saveData(data) {
    (historyDeta.length === 10) && (historyDeta.shift()); // ä¸Šé™ä¸ºå‚¨å­˜10æ­¥ï¼Œå¤ªå¤šäº†æ€•æŒ‚æŽ‰
    historyDeta.push(data);
}

//è®°å½•ä¸Šæ¬¡ä½ç½®æ•°æ®
let last
//å£°æ˜Žç”»çº¿å‡½æ•°
function drawLine(x1, y1, x2, y2) {
    ctx.beginPath()
    ctx.moveTo(x1, y1)
    ctx.lineTo(x2, y2)
    ctx.stroke()
}

// èŽ·å–æ˜¯å¦è§¦æ‘¸
let isTouchDevice = "ontouchstart" in document.documentElement

//ç»˜åˆ¶ä¸€ä¸ªå¡«å……äº†å†…å®¹çš„åœ†å½¢
if (isTouchDevice) {    //ç§»åŠ¨ç«¯
    canvas.ontouchstart = (e) => {
        last = [e.touches[0].clientX, e.touches[0].clientY]
        painting = true
    }
    canvas.ontouchmove = (e) => {

        if (painting === true) {
            drawLine(last[0], last[1], e.touches[0].clientX, e.touches[0].clientY)
            last = [e.touches[0].clientX, e.touches[0].clientY]
        } else {
            // console.log("åœæ­¢")
        }
    }
} else {   //PCç«¯
    canvas.onmousedown = (e) => {
        painting = true
        last = [e.clientX, e.clientY]

        // åœ¨è¿™é‡Œå‚¨å­˜ç»˜å›¾è¡¨é¢
        let firstDot
        firstDot = ctx.getImageData(0, 0, canvas.width, canvas.height);
        saveData(firstDot);


    }
    canvas.onmousemove = (e) => {
        if (painting === true) {
            // console.log(last);
            drawLine(last[0], last[1], e.clientX, e.clientY)

            last = [e.clientX, e.clientY]
        } else {
            // console.log("åœæ­¢")

        }
    }
    canvas.onmouseup = () => {
        painting = false
    }
}

