//äº‹ä¸è¿‡ä¸‰
let wrapperBut = document.getElementById("wrapper")
wrapperBut.addEventListener('click', (e) => {
    console.log(e.target.tagName);
    if (e.target.type === 'submit') {

        ctx.strokeStyle = e.target.id
        lastsStrokeStyle = e.target.id

    } else if (e.target.tagName === 'BUTTON' || e.target.tagName === 'svg' || e.target.tagName === 'use') {
       
        switch (e.target.id) {
            case 'ç”»ç¬”':
                console.log('é€‰æ‹©ç”»ç¬”');
                ctx.strokeStyle = lastsStrokeStyle
                break;
            case 'æ©¡çš®':
                console.log('é€‰æ‹©æ©¡çš®');
                ctx.strokeStyle = 'white'

                break;
            case 'æ¸…é™¤':
                console.log('é€‰æ‹©æ¸…é™¤');
                ctx.save();
                ctx.setTransform(1, 0, 0, 1, 0, 0);
                ctx.clearRect(0, 0, canvas.width, canvas.height);
                ctx.restore();
                break;
            case 'æ’¤å›ž':
                console.log('é€‰æ‹©æ’¤å›ž');
                if (historyDeta.length < 1) return false;
                ctx.putImageData(historyDeta[historyDeta.length - 1], 0, 0);
                historyDeta.pop()
                break;
            case 'ä¿å­˜':
                console.log('é€‰æ‹©ä¿å­˜');
                let imgUrl = canvas.toDataURL("image/png");
                let saveA = document.createElement("a");
                document.body.appendChild(saveA);
                saveA.href = imgUrl;
                saveA.download = "zspic" + (new Date).getTime();
                saveA.target = "_blank";
                saveA.click();
                break;
            default:
                break;
        }
    } else {
        ctx.lineWidth = e.target.value //8
    }

})

